export const categoryEngToOrk = name => {
  switch (name.trim()) {
    case 'Products':
      return '';
    case '':
      return '';
    case 'Entertainment':
      return '';
    case 'Health':
      return '';
    case 'Transport':
      return '';
    case 'Housing':
      return '  ';
    case 'Technics':
      return '';
    case 'Communal and communication':
      return '  ';
    case 'Sport and hobby':
      return '  ';
    case 'Education':
      return '';
    case 'Other':
      return '';
    case 'Salary':
      return '/';
    case 'Additional income':
      return '. ';
    default:
      break;
  }
};

export const categoryOrkToEng = name => {
  switch (name.trim()) {
    case '':
      return 'Products';
    case '':
      return '';
    case '':
      return 'Entertainment';
    case '':
      return 'Health';
    case '':
      return 'Transport';
    case '  ':
      return 'Housing';
    case '':
      return 'Technics';
    case '  ':
      return 'Communal and communication';
    case '  ':
      return 'Sport and hobby';
    case '':
      return 'Education';
    case '':
      return 'Other';
    case '/':
      return 'Salary';
    case '. ':
      return 'Additional income';
    default:
      break;
  }
};

